









































































































body {
  margin: 0;
}
.demand-detail {
  position: relative;
}
.main {
  height: 1500px;
  //padding: 20px;
  .sub-title {
    text-align: left;
    margin-top: 30px;
    margin-bottom: 20px;
    .show-demand {
      font-size: 25px;
      color: black;
    }
  }
  .third-title {
    text-align: left;
    margin-top: 30px;
    margin-bottom: 20px;
    margin-left: 30px;
    .show-demand {
      font-size: 20px;
      color: black;
    }
  }
  .enterprise-demand-background {
    margin-left: 40px;
    margin-right: 40px;
    height: 80px;
    //border: 2px solid #e5e7e9;
    text-align: left;
    text-indent: 2em;
    font-size: 17px;
  }
  .demand-description {
    margin-left: 40px;
    margin-right: 40px;
    height: 450px;
    //border: 2px solid #e5e7e9;
    text-align: left;
    text-indent: 2em;
    font-size: 17px;
  }
  .supplier-requirement {
    margin-left: 40px;
    margin-right: 40px;
    height: 50px;
    //border: 2px solid #e5e7e9;
    text-align: left;
    text-indent: 2em;
    font-size: 17px;
  }
  .declare {
    height: 50px;
    width: 350px;
    font-size: 20px;
    margin-bottom: 15px;
    margin-top: 5px;
  }
  .demand {
    //border: 2px solid #e5e7e9;
    .demand-image {
      height: 100%;
      //border: 2px solid #e5e7e9;
    }
    .demand-text {
      height: 100%;
      //border: 2px solid #e5e7e9;
      .demand-text-title-row {
        font-size: 25px;
        font-weight: bold;
        margin-top: 30px;
        margin-bottom: 30px;
      }
      .demand-text-row {
        font-size: 18px;
        margin-top: 20px;
        margin-bottom: 15px;
        text-align: left;
      }
      .demand-button {
        height: 50px;
        width: 300px;
        font-size: 20px;
        margin-bottom: 15px;
        margin-top: 15px;
      }
    }
  }
}
