@charset "UTF-8";
body {
  margin: 0; }

.achievement {
  position: relative; }
  .achievement .intro-detail {
    display: flex;
    height: 150px;
    justify-content: center; }
    .achievement .intro-detail .intro-detail-img {
      margin: 0 15px;
      padding: 5px; }
  .achievement .transform {
    text-align: left; }
    .achievement .transform .transform-main {
      display: flex; }
      .achievement .transform .transform-main .transform-main-left {
        flex: 1;
        margin-right: 20px; }
        .achievement .transform .transform-main .transform-main-left div {
          display: flex;
          margin: 20px 0; }
          .achievement .transform .transform-main .transform-main-left div p {
            margin-top: 0; }
      .achievement .transform .transform-main .transform-main-right {
        flex: 1; }
        .achievement .transform .transform-main .transform-main-right div {
          margin: 5px;
          padding: 5px;
          border: 1px solid #ccc;
          border-radius: 5px;
          width: 80%;
          text-align: center; }
          .achievement .transform .transform-main .transform-main-right div h3 {
            margin: 0;
            color: #570000; }
          .achievement .transform .transform-main .transform-main-right div p {
            margin: 0; }
  .achievement .step .step-detail {
    display: flex;
    justify-content: space-between;
    color: #000; }
  .achievement .step .report {
    display: flex;
    margin-top: 30px; }
    .achievement .step .report .report-title {
      width: 200px;
      color: #aca1fb;
      text-shadow: 3px 3px 3px #ccc; }
    .achievement .step .report .report-content {
      flex: 1;
      color: #000;
      text-align: left; }
      .achievement .step .report .report-content .el-carousel__item {
        background: #fff; }
  .achievement .consult {
    display: flex; }
    .achievement .consult .consult-left {
      position: relative;
      flex: 1;
      text-align: left; }
      .achievement .consult .consult-left span {
        margin-right: 15px; }
      .achievement .consult .consult-left .expert-container {
        display: flex; }
        .achievement .consult .consult-left .expert-container .expert-item {
          flex: 1;
          margin: 10px;
          padding: 10px;
          border: 1px solid #ccc;
          text-align: center; }
          .achievement .consult .consult-left .expert-container .expert-item .expert-name {
            font-size: 1rem; }
          .achievement .consult .consult-left .expert-container .expert-item .detail-container {
            margin: 0 auto;
            border: 1px solid #ccc;
            width: 90px; }
            .achievement .consult .consult-left .expert-container .expert-item .detail-container .expert-area {
              padding: 5px 0;
              background: #ccc;
              font-size: 0.8rem; }
            .achievement .consult .consult-left .expert-container .expert-item .detail-container .expert-college {
              padding: 5px 0;
              font-size: 0.8rem; }
          .achievement .consult .consult-left .expert-container .expert-item .consulting {
            margin-top: 10px;
            color: #570000;
            font-weight: bold; }
      .achievement .consult .consult-left .more {
        position: absolute;
        top: -5px;
        right: 0;
        background: #ccc;
        padding: 5px 10px;
        border-radius: 5px;
        cursor: pointer; }
    .achievement .consult .consult-right {
      position: relative;
      flex: 1;
      padding-bottom: 40px; }
      .achievement .consult .consult-right h3 {
        margin: 0; }
      .achievement .consult .consult-right .college-container {
        display: flex;
        flex-wrap: wrap; }
      .achievement .consult .consult-right .college-item {
        display: flex;
        border: 1px solid #ccc;
        margin: 5px;
        padding: 5px;
        width: 45%;
        text-align: left; }
        .achievement .consult .consult-right .college-item .item-avatar {
          box-sizing: border-box;
          margin-right: 10px;
          padding-top: 10px; }
        .achievement .consult .consult-right .college-item .item-info h4, .achievement .consult .consult-right .college-item .item-info p {
          margin: 5px 0; }
        .achievement .consult .consult-right .college-item .item-info p {
          font-size: 0.8rem; }
      .achievement .consult .consult-right .more {
        position: absolute;
        bottom: 0;
        right: 10px;
        background: #ccc;
        padding: 5px 10px;
        border-radius: 5px;
        cursor: pointer; }
  .achievement .comments {
    text-align: left; }
    .achievement .comments h3 {
      color: #570000; }
    .achievement .comments .title {
      text-align: center;
      color: #570000; }
    .achievement .comments div {
      display: flex;
      margin: 10px 0; }
      .achievement .comments div p {
        margin: 0;
        margin-top: -5px; }
  .achievement .step {
    color: #570000; }
    .achievement .step .find-more {
      width: 400px;
      margin: 0 auto;
      cursor: pointer;
      font-size: 1.2rem; }
    .achievement .step .comment-step {
      display: flex;
      flex-wrap: wrap; }
      .achievement .step .comment-step .step-item {
        padding: 0 10px;
        margin: 20px;
        width: 25%;
        border: 1px solid #fff;
        border-radius: 50%;
        background: #f4dede; }
        .achievement .step .comment-step .step-item h3 {
          margin: 5px; }
        .achievement .step .comment-step .step-item p {
          margin: 5px; }
  .achievement .example {
    text-align: left; }

.header-image {
  position: relative;
  height: 400px;
  width: 100%;
  margin-top: 0px;
  background-image: url(../../../static/img/banner-achievement.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  -moz-background-size: 100% 100%;
  background-position: center; }

.intro {
  height: 200px;
  text-align: center; }
  .intro .intro-text {
    font-size: 1rem;
    color: #666;
    text-align: center; }

.technology-container {
  padding-left: 20px;
  padding-right: 30px; }
  .technology-container .technology-tab-container .technology-item {
    padding: 5px;
    border-top: 1px solid #ccc;
    text-align: left; }
    .technology-container .technology-tab-container .technology-item .technology-title {
      font-size: 1rem; }
    .technology-container .technology-tab-container .technology-item .technology-title-tag {
      margin: 5px; }
    .technology-container .technology-tab-container .technology-item .technology-tags {
      display: inline-block;
      margin: 5px;
      font-size: 0.8rem;
      padding: 5px;
      background: #f4f6f6;
      border: 1px solid #ddd;
      border-radius: 5px; }
    .technology-container .technology-tab-container .technology-item .technology-content {
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical; }
    .technology-container .technology-tab-container .technology-item .content-more {
      padding: 10px 0;
      text-align: center;
      font-family: "微软雅黑";
      cursor: pointer; }
  .technology-container .el-tabs__item {
    font-size: 1rem; }
  .technology-container .technology-find-more-container {
    border-top: 1px solid #ccc;
    padding-top: 20px;
    text-align: center; }
    .technology-container .technology-find-more-container .technology-find-more {
      display: inline-block;
      background: #000;
      border-radius: 5px;
      padding: 5px 10px;
      color: #fff;
      cursor: pointer; }
      .technology-container .technology-find-more-container .technology-find-more .find-more-icon {
        vertical-align: middle; }

.bottom-container {
  margin: 20px 0;
  color: #f3c19b; }
  .bottom-container .bottom-item {
    margin: 0 auto;
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 10px;
    width: 300px;
    height: 150px;
    font-size: 1rem; }
    .bottom-container .bottom-item .item-text {
      margin-top: 10px; }

.example .example-more {
  text-align: right;
  font-size: 1.2rem;
  cursor: pointer; }

.example .example-item-container {
  display: flex; }
  .example .example-item-container .example-item {
    flex: 1;
    margin: 0 10px; }
    .example .example-item-container .example-item p {
      text-indent: 2rem;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 4;
      -webkit-box-orient: vertical; }

.sub-title {
  font-size: 30px;
  margin-top: 10px;
  margin-bottom: 30px; }

.policy {
  margin-top: 10px;
  margin-bottom: 50px; }
  .policy .policy-main {
    height: 500px;
    border: 1px solid #ccc; }
    .policy .policy-main .left-title {
      font-size: 18px;
      color: brown;
      margin-top: 20px;
      margin-bottom: 5px;
      margin-left: 20px;
      margin-right: 20px;
      cursor: pointer; }
    .policy .policy-main .left-content {
      font-size: 15px;
      margin-top: 5px;
      margin-bottom: 0px;
      margin-left: 20px;
      margin-right: 20px; }
  .policy .policy-title {
    font-size: 25px;
    margin-top: 10px;
    margin-bottom: 10px; }
  .policy .policy-text {
    font-size: 20px;
    margin-top: 30px;
    margin-bottom: 30px; }

.el-carousel__item h3 {
  color: #475669;
  font-size: 14px;
  opacity: 0.75;
  line-height: 150px;
  margin: 0; }

.el-carousel__item:nth-child(2n) {
  background-color: #99a9bf; }

.el-carousel__item:nth-child(2n + 1) {
  background-color: #d3dce6; }

.search-area {
  float: left; }

.policy-search-title {
  float: left;
  margin-top: 5px;
  margin-bottom: 10px;
  text-align: left;
  font-weight: bold; }

.policy-search-content {
  float: left;
  margin-top: 10px;
  margin-bottom: 5px;
  text-align: left; }
