@charset "UTF-8";
body {
  margin: 0; }

.index {
  position: relative; }

/* 主内容区 */
main {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  min-height: 500px;
  border: solid 40px #e9ecf1;
  background-color: #fcfcfc; }

main .main-left {
  text-align: center;
  width: 200px;
  float: left; }

main .main-right {
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  background-color: #fff;
  padding: 50px 70px; }

main .el-menu {
  background-color: transparent !important; }

.intro-pic {
  height: 280px;
  width: 100%;
  padding-top: 5px;
  padding-left: 50px;
  padding-right: 100px;
  background-image: url(../../static/img/service.png);
  background-repeat: no-repeat;
  background-size: 26% 130%;
  -moz-background-size: 100% 100%;
  background-position: center; }
  .intro-pic .intro-sub-title {
    font-weight: bold;
    font-size: 20px;
    color: #3498db;
    margin-bottom: 0px;
    font-family: 'Arial', 'Microsoft YaHei', '黑体', '宋体', sans-serif; }
  .intro-pic .intro-sub-content {
    font-size: 15px;
    margin-top: 10px;
    font-family: 'Arial', 'Microsoft YaHei', '黑体', '宋体', sans-serif; }
  .intro-pic .intro-first {
    height: 20%;
    width: 100%; }
    .intro-pic .intro-first .intro-first-1 {
      height: 100%; }
      .intro-pic .intro-first .intro-first-1 .intro-first-technology {
        text-align: right;
        margin-top: 50px; }
    .intro-pic .intro-first .intro-first-2 {
      height: 100%; }
      .intro-pic .intro-first .intro-first-2 .intro-first-policy {
        text-align: left;
        margin-top: 50px; }
  .intro-pic .intro-second {
    height: 35%;
    width: 100%; }
  .intro-pic .intro-third {
    height: 30%;
    width: 100%; }
    .intro-pic .intro-third .intro-third-2 {
      height: 100%; }
      .intro-pic .intro-third .intro-third-2 .intro-third-service {
        text-align: right;
        margin-top: 0px; }
    .intro-pic .intro-third .intro-third-2 {
      height: 100%; }
      .intro-pic .intro-third .intro-third-2 .intro-third-science {
        text-align: left;
        margin-top: 0px; }

.intro-icon {
  padding-top: 30px;
  padding-left: 50px;
  padding-right: 100px; }

.header-image {
  height: 430px;
  width: 100%;
  margin-top: 0px;
  background-image: url(../../static/img/banner-overview.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  -moz-background-size: 100% 100%;
  background-position: center; }
  .header-image .header-select {
    line-height: 500px; }
  .header-image .header-select input.el-input__inner {
    height: 80%;
    border-radius: 15px;
    opacity: 0.65; }
  .header-image .header-select .el-input--suffix {
    width: 30%; }
    .header-image .header-select .el-input--suffix .el-input__icon {
      color: black;
      width: 50px; }

.intro {
  height: 200px;
  text-align: center; }
  .intro .intro-text {
    font-size: 1rem;
    color: #666;
    text-align: center; }
  .intro .intro-detail {
    height: 200px; }
    .intro .intro-detail .intro-detail-idea {
      height: 43%;
      width: 90%;
      margin: 0 auto;
      position: relative;
      top: 22%;
      border: 2px solid #e5e7e9;
      border-radius: 20px;
      padding: 10px; }
    .intro .intro-detail .intro-detail-arrow {
      height: 55%;
      width: 100%;
      margin: 0 auto;
      position: relative;
      top: 22%;
      border-radius: 20px; }
    .intro .intro-detail .intro-detail-img {
      height: 55%;
      width: 55%;
      margin: 0 auto;
      position: relative;
      top: 15%;
      cursor: pointer; }

.technology-container {
  padding-left: 20px;
  padding-right: 30px; }
  .technology-container .technology-tab-container .technology-item {
    padding: 5px;
    border-top: 1px solid #ccc;
    text-align: left; }
    .technology-container .technology-tab-container .technology-item .technology-title {
      font-size: 1rem; }
    .technology-container .technology-tab-container .technology-item .technology-title-tag {
      margin: 5px; }
    .technology-container .technology-tab-container .technology-item .technology-tags {
      display: inline-block;
      margin: 5px;
      font-size: 0.8rem;
      padding: 5px;
      background: #f4f6f6;
      border: 1px solid #ddd;
      border-radius: 5px; }
    .technology-container .technology-tab-container .technology-item .technology-content {
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical; }
    .technology-container .technology-tab-container .technology-item .content-more {
      padding: 10px 0;
      text-align: center;
      font-family: '微软雅黑';
      cursor: pointer; }
  .technology-container .el-tabs__item {
    font-size: 1rem; }
  .technology-container .technology-find-more-container {
    border-top: 1px solid #ccc;
    padding-top: 20px;
    text-align: center; }
    .technology-container .technology-find-more-container .technology-find-more {
      display: inline-block;
      background: #000;
      border-radius: 5px;
      padding: 5px 10px;
      color: #fff;
      cursor: pointer; }
      .technology-container .technology-find-more-container .technology-find-more .find-more-icon {
        vertical-align: middle; }

.bottom-container {
  margin: 20px 0;
  color: #f3c19b; }
  .bottom-container .bottom-item {
    margin: 0 auto;
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 10px;
    width: 300px;
    height: 150px;
    font-size: 1rem; }
    .bottom-container .bottom-item .item-text {
      margin-top: 10px; }

.sub-title {
  font-size: 30px;
  margin-top: 10px;
  margin-bottom: 30px; }

.policy {
  margin-top: 10px;
  margin-bottom: 50px; }
  .policy .policy-main {
    border: 1px solid #ccc; }
    .policy .policy-main .left-title {
      font-size: 18px;
      color: brown;
      margin-top: 20px;
      margin-bottom: 5px;
      margin-left: 20px;
      margin-right: 20px;
      cursor: pointer; }
    .policy .policy-main .left-content {
      font-size: 15px;
      margin-top: 5px;
      margin-bottom: 0px;
      margin-left: 20px;
      margin-right: 20px; }
  .policy .policy-title {
    font-size: 25px;
    margin-top: 10px;
    margin-bottom: 10px; }
  .policy .policy-text {
    font-size: 20px;
    margin-top: 30px;
    margin-bottom: 30px; }

.el-carousel__item h3 {
  color: #475669;
  font-size: 14px;
  opacity: 0.75;
  line-height: 150px;
  margin: 0; }

.el-carousel__item:nth-child(2n) {
  background-color: #99a9bf; }

.el-carousel__item:nth-child(2n + 1) {
  background-color: #d3dce6; }

.search-area {
  float: left; }

.policy-search-title {
  float: left;
  margin-top: 5px;
  margin-bottom: 10px;
  text-align: left;
  font-weight: bold; }

.policy-search-content {
  float: left;
  margin-top: 10px;
  margin-bottom: 5px;
  text-align: left; }
