@charset "UTF-8";
body {
  margin: 0; }

#policy-detail {
  position: relative; }

.main {
  height: 2300px; }
  .main .breadcrumb {
    margin-top: 15px;
    margin-bottom: 10px; }
    .main .breadcrumb .el-breadcrumb {
      font-size: 15px; }
  .main .policy-page {
    height: 55%;
    border: 1px solid #ccc;
    margin-left: 100px;
    margin-right: 100px;
    margin-top: 50px; }
    .main .policy-page .policy-page-title {
      font-weight: bold;
      font-size: 30px;
      margin-top: 40px;
      margin-bottom: 30px; }
    .main .policy-page .policy-page-time {
      text-align: left; }
    .main .policy-page .policy-page-origin {
      text-align: right; }
    .main .policy-page .policy-page-title-sub-1 {
      text-align: left;
      margin-left: 30px;
      font-weight: bold;
      font-size: 20px;
      color: brown; }
    .main .policy-page .policy-page-title-sub-2 {
      text-align: left;
      margin-left: 30px;
      font-size: 15px; }
    .main .policy-page .policy-page-text {
      text-align: left;
      margin-left: 30px;
      margin-right: 30px;
      font-size: 15px; }
  .main .policy-origin {
    text-align: left;
    margin-left: 100px;
    color: #b03a2e;
