    margin-left: 100px;
    color: #2e86c1;
    font-size: 20px;
    font-family: "Helvetica Neue", "PingFang SC", Arial, sans-serif; }
  .main .sub-title {
    text-align: left;
    margin-top: 30px;
    margin-bottom: 10px; }
    .main .sub-title .process {
      font-size: 25px;
      color: #fbc02d;
      margin-top: 30px; }
  .main .flow-chart {
    height: 15%;
    margin-left: 60px;
    margin-right: 60px;
    margin-top: 50px;
    margin-bottom: 0px; }
    .main .flow-chart .flow-chart-row {
      height: 100%; }
      .main .flow-chart .flow-chart-row .flow-chart-col-1 {
