            .main .flow-chart .flow-chart-row .flow-chart-col-1 .flow-chart-content .el-card__body .flow-chart-content-text {
              margin-left: 25px;
              margin-right: 25px;
              font-weight: bold;
              font-size: 18px;
              color: #3f51b5;
              text-align: left;
              font-family: "Arial", "Microsoft YaHei", "黑体", "宋体", sans-serif; }
      .main .flow-chart .flow-chart-row .flow-chart-col-2 {
        height: 100%; }
        .main .flow-chart .flow-chart-row .flow-chart-col-2 .flow-chart-arrow-x {
