        font-family: "Arial", "Microsoft YaHei", "黑体", "宋体", sans-serif; }
      .main .similar-policy .similar-policy-row .similar-policy-col-3 {
        height: 100%;
        text-align: left;
        font-family: "Arial", "Microsoft YaHei", "黑体", "宋体", sans-serif; }
