





































































































































































































































































































































body {
  margin: 0;
}
.policy {
  position: relative;
}
.header-image {
  height: 400px;
  width: 100%;
  margin-top: 0px;
  background-image: url(../../../static/img/banner-policy.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  -moz-background-size: 100% 100%;
  background-position: center;
  .header-select {
    line-height: 500px;
    // background-color: #1c2833;
  }
  .header-select input.el-input__inner {
    height: 80%;
    border-radius: 15px;
    opacity: 0.65;
  }
  .header-select .el-input--suffix {
    width: 30%;
    .el-input__icon {
      color: black;
      width: 50px;
    }
  }
}
.main {
  height: 2700px;
  //padding: 20px;
  .breadcrumb {
    margin-top: 5px;
    margin-bottom: 10px;
    .el-breadcrumb {
      font-size: 15px;
    }
  }
  .sub-title {
    text-align: left;
    margin-top: 10px;
    margin-bottom: 15px;
    .show-policy {
      font-size: 25px;
      color: black;
    }
    .show-service {
      font-size: 25px;
      color: black;
    }
    .show-science {
      font-size: 25px;
      color: black;
    }
    .show-success {
      font-size: 25px;
      color: black;
    }
  }
  .policy {
    height: 400px;
    margin-top: 20px;
    margin-bottom: 5px;
    .policy-detail {
      height: 100%;
      padding: 20px;
      .policy-image {
        height: 100%;
        padding: 10px;
        //border: 1px solid #e5e7e9;
      }
      .policy-content {
        height: 100%;
        padding: 10px;
        //border: 1px solid #e5e7e9;
      }
    }
  }
  .declare {
    height: 50px;
    width: 240px;
    font-size: 20px;
    margin-bottom: 15px;
  }
  .divider {
    margin-top: 5px;
    margin-bottom: 5px;
  }
  .service-1 {
    height: 150px;
    margin-top: 5px;
    margin-bottom: 5px;
    .service-image {
      height: 100%;
      // border: 1px solid #e5e7e9;
      padding-top: 30px;
      padding-left: 120px;
      padding-right: 120px;
    }
  }
  .service-2 {
    height: 100px;
    margin-top: 20px;
    margin-bottom: 5px;
    .service-content {
      text-indent: 2em;
      height: 100%;
      padding: 10px;
      //border: 1px solid #e5e7e9;
      text-align: left;
    }
  }
  .science-policy {
    margin-top: 50px;
    .science-policy-title {
      margin-top: 7px;
      text-align: right;
    }
    .science-policy-content {
      text-align: left;
    }
    .science-policy-area-1 {
      margin-top: 20px;
    }
    .science-policy-area-2 {
      margin-top: 20px;
    }
    .science-policy-department {
      margin-top: 20px;
    }
    .search-first {
      margin-top: 20px;
    }
    .science-policy-effective {
      margin-top: 20px;
    }
    .science-policy-border {
      height: 480px;
      overflow-y: scroll;
      margin-top: 40px;
      margin-left: 20px;
      margin-right: 20px;
      margin-bottom: 40px;
      .policy-search-title {
        float: left;
        margin-top: 5px;
        margin-bottom: 10px;
        text-align: left;
        font-weight: bold;
      }
      .policy-search-content {
        float: left;
        margin-top: 10px;
        margin-bottom: 5px;
        text-align: left;
      }
    }
  }
  .success {
    margin: 20px;
    .success-image {
      height: 220px;
      //border: 1px solid #e5e7e9;
      .success-image-sub {
        height: 100%;
        //border: 1px solid #e5e7e9;
      }
    }
    .success-text {
      margin-top: 20px;
      height: 150px;
      //border: 1px solid #e5e7e9;
      .success-text-sub {
        height: 100%;
        //border: 1px solid #e5e7e9;
        text-align: left;
        text-indent: 2em;
      }
    }
  }
  .intro-detail {
    height: 100%;
    .intro-detail-img {
      height: 55%;
      width: 55%;
      margin: 0 auto;
      position: relative;
      top: 15%;
      cursor: pointer;
    }
  }
}
