   position: relative;
  .lbs-container {
    padding: 10px 30px;
    text-align: left;
    h2{
      text-align: center;
    }
    .map-container {
      margin-top: 10px;
      height: 300px;
    }
    p {
      text-indent: 2rem;
    }
  }
}
.conponents {
  .conponents-tab {
    text-align: left;
  }
  .park-design {
    display: flex;
    flex-wrap: wrap;
    align-content: space-between;
    .column {
      flex-basis: 100%;
      display: flex;
      justify-content: space-around;
      .item {
        margin: 10px;
        padding: 10px;
        width: 300px;
        border: 1px solid #ccc;
        background: rgb(244, 222, 222);
      }
    }
  }
  .conponents-image {
    display: flex;
    .image-item {
      margin-right: 20px;
      width: 30%;
    }
  }
}
.show-container {
  padding: 0 20px;
  text-align: left;
  h2 {
    text-align: center;
  }
  .tag {
    margin: 5px;
    background: #fff;
    color: #000;
  }
}
.sign-up-container {
    margin: 30px auto;
    background: rgb(186, 226, 230);
    padding: 10px;
    width: 300px;
    text-align: center;
    cursor: pointer;
    .sign-up-image {
      vertical-align: middle;
    }
  }
