













































































































































































































#platform {
  background: rgb(240, 232, 232);
  position:relative;
  .head-image {
    box-sizing: border-box;
    padding: 50px;
    height: 300px;
    background: url(../../../static/img/banner-technology.jpeg) no-repeat center center;
    background-size: 100% 100%;
    color: #fff;
    .head-search {
      margin: 0 auto;
      width: 300px;
      height: 50px;
    }
  }
  .filter-container {
    text-align: left;
    padding: 0 20px;
    .filter-domain {
      display: flex;
      margin-bottom: 10px;
      .domain-tag {
        margin: 5px;
        cursor: pointer;
      }
    }
    .filter-area {
      display: flex;
      .area-tag {
        margin: 5px;
        cursor: pointer;
      }
    }
    .filter-label{
        width: 90px;
        padding-top: 10px;
      }
  }
  .btn-container{
      padding-left: 20px;
      text-align: left;
      .time{
        position: relative;
      .arrow-up {
          position: absolute;
          right: 5px;
          top: 5px;
      }
      .arrow-down {
          position: absolute;
          right: 5px;
          bottom: 5px;
      }
      }
  }
  .content-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    .content-item {
      position: relative;
      margin: 10px;
      background: #fff;
      width: 28%;
      padding: 10px;
      padding-bottom: 60px;
      text-align: left;
      cursor: pointer;
      .content-title{
        text-align: center;
font-size: 1.2rem;
      }
      .content-main {
        display: flex;
        .content-image {
          // float: left;
          margin-right: 10px;
        }
        .content-text {
          .content-para{
            text-indent: 2rem;
            overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 6;
        -webkit-box-orient: vertical;
          }
        }
      }
      .content-tag{
        position: absolute;
        bottom: 10px;
      }
    }
  }
  .find-more-container {
    margin: 10px 0;
    cursor: pointer;
    .find-more {
      vertical-align: middle;
    }
  }
}
