























































































































.header {
  height: 60px;
  .el-menu.el-menu--horizontal {
    border-bottom: solid 0px white;
  }
  .header-brand {
    font-size: 200%;
    color: #fff;
    line-height: 60px;
    background-color: #1c2833;
    text-align: left;
    padding-left: 15px;
  }
  .header-select {
    line-height: 60px;
    background-color: #1c2833;
  }
  .header-select input.el-input__inner {
    height: 80%;
    border-radius: 20px;
  }
  .header-select .el-input--suffix {
    width: 80%;
  }
}
